*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 70;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  text-align: center;
  border-radius: 1px solid grey;
  margin-top: 6px;
  margin-right: 10px;
  overflow:hidden;
}

.card p{
  padding: 10px;
  text-align: center;
}

.card img {
  max-height: 170px;
}